.App .container .left-panel {
    height: 100vh;
    text-align: right;
    background: #F4F4F4;
    padding-top: 20px;
}
.App .container .right-panel {
    height: 100vh;
    text-align: center;
    background: #2D2F31;
    padding-top: 20px;
    color: #fff;
}
.App .login-status {
    text-align: left;
    width: 50%;
    margin: 0 auto;
}
.App .login-status .login-status-msg {
    font-size: .8em;
    margin: 0;
}
.App .login-status .login-status-id {
    font-size: 1em;
    margin: 0;
}
.App .counter {
    margin-top: 70vh;
}
.App .counter .counter-text {
    font-size: 1em;
    margin: 0;
}
.App .counter .counter-time {
    font-size: 2em;
    margin: 0;
}
.btn.submit-test {
    padding: 6px 50px;
    border-radius: 20px;
}